.input-style {
  width: 100%;
  height: 38px;
  padding: 6px 12px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  border-radius: 3px;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.input-style:focus {
  border-color: #1F90E6;
  -webkit-box-shadow: 0 0 8px rgba(102, 175, 233, 0.6);
          box-shadow: 0 0 8px rgba(102, 175, 233, 0.6);
}
.textarea-style {
  line-height: 1.42857143;
  width: 100%;
  padding: 6px 12px;
  border-radius: 3px;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}
.textarea-style:focus {
  border-color: #1F90E6;
  -webkit-box-shadow: 0 0 8px rgba(102, 175, 233, 0.6);
          box-shadow: 0 0 8px rgba(102, 175, 233, 0.6);
}
.select-style {
  width: 100%;
  height: 33px;
  padding: 5px 12px;
  background-color: #fff;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  border-radius: 3px;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.select-style:focus {
  border-color: #1F90E6;
  -webkit-box-shadow: 0 0 8px rgba(102, 175, 233, 0.6);
          box-shadow: 0 0 8px rgba(102, 175, 233, 0.6);
}
.hide {
  opacity: 0;
  pointer-events: none;
}
.show {
  opacity: 1;
  pointer-events: auto;
}
.Explore .content {
  padding: 24px;
}
.Explore .content > .ant-layout-content {
  background: #fff;
  padding: 24px;
  margin: 0;
  min-height: 580px;
}
.Explore .content td > .ant-btn {
  margin-right: 7px;
}



